html {
   scroll-behavior: smooth;
}
.resumeintro {
    background: linear-gradient(135deg, #31473A 80%, #3e5c48 100%);
    border-radius: 18px;
    box-shadow: 0 8px 32px rgba(0,0,0,0.18);
    padding: 2rem 0.5rem;
    margin: 24px auto;
    max-width: 900px;
    min-height: 400px;
    position: relative;
}
.knowmore{
    max-width: 800px;
    color: rgb(0, 78, 39);
    border: none;
    background-color:#b0b0b0;
    margin: 0 auto;
    padding: 1rem;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
     margin-top: 0.5rem;
    transition: background-color 0.3s ease;
    text-decoration: none;
}
.knowmore:hover{
    background-color: #00a767;
}
.profile {
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: white;
    text-align: center;
    padding: 1rem 0;
    line-height: 1rem;
    margin-bottom: 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center
    
}
.profile h2 {
  white-space: nowrap;
  text-align: center;
  font-size: 2rem;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.profile button{
    margin-top: 1rem;
    padding: 0.5rem 1.5rem;
    font-size: 1rem;
}
@media (max-width: 768px) {
  .resumeintro {
    flex-direction: column;
    align-items: center;
  }
  .profile {
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
    align-items: center;
  }
  .social-icons{
    flex-direction: row;
    justify-content: center;
  }
  .ProfilePic {
  width: 200px;
  height: auto;
  margin: 20px auto;
  }
 .menu-bar ul {
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  }
}
.ProfilePic {
  width: 300px;
  height: auto;
  margin: 10px auto;
}
.social-icons-container {
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 1001;
    display: flex;
    align-items: center;
    gap: 10px;
}

.icon-menu {
    cursor: pointer;
    font-size: 2rem;
    color: rgb(219, 255, 197);
}
.close-icon{
    position: relative;
    cursor: pointer;
    color: rgb(255, 26, 26);
}
.full-width{
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 300px;
  max-width: 90vw;
  background-color: #EDF4F2;
  box-shadow: 2px 0 16px rgba(0,0,0,0.15);
  z-index: 2000;
  overflow-y: auto;
  border-top-right-radius: 16px;
  border-bottom-right-radius: 16px;
}
.all-icons a {
  margin-top: 10px;
  padding: 2px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}
.all-icons a svg {
  width: 25px;
  height: 25px;
  padding: 0px;
  fill: #12714a; /* LinkedIn color */
}
.social-icons{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1px;
    padding: 20px 0;
}
.menu-button{
  margin-bottom: 10px;
  background-color: #0077b5;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
}
.side-image{
    margin-top: 30px;
    border-radius: 50%;
    border: 2px solid rgb(10, 120, 108);
    width: 150px;
    height: 160px;
}
.menu-bar {
  background-color: #ffffff;
  padding: 1rem;
  text-align: center;
  margin-top: 30px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}
.menu-bar ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
  margin: 0;
}
.menu-bar li a {
  padding: auto 20px; ;
  display: flex;
  margin-right: 40px;
  flex-direction: column;
  text-decoration: none;
  color: #09c677;
  font-weight: 600;
  transition: color 0.3s ease;
  }
.menu-bar li a:hover {
  color: #12714a;
}
@media (max-width: 480px) {
  .resumeintro {
    flex-direction: column;
    padding: 1rem;
  }

  .profile h2 {
    font-size: 1.5rem;
  }

  .knowmore {
    font-size: 0.9rem;
    padding: 8px 16px;
  }
}
